import * as types from './../constants/ActionTypes'

var data = JSON.parse(localStorage.getItem("CART"))

var initialState = []

if (data) {
    initialState = data
}


const myReducer = (state = initialState , action) =>{
    switch (action.type){
        case types.ADD_TO_CART :{
            var checkProductInCart = false
            for (let index = 0; index < state.length; index++) {
                if (action.product.id === state[index].product.id) {
                    checkProductInCart = true
                    break;
                }
            }
            if (checkProductInCart) {
                var index = state.map(function(el) {
                    return el.product.id;
                  }).indexOf(action.product.id);
                state[index].quantity = parseInt(state[index].quantity) +1
            }else{
                var item = {
                    product : action.product,
                    quantity : 1
                }
                state.push(item)
            }
            localStorage.setItem("CART" , JSON.stringify(state))
            return [...state]
        }
        case types.DELETE_ITEM :{
            var index = state.map(function(el) {
                return el.product.id;
              }).indexOf(action.cart.id);
            state.splice(index,1);
            localStorage.setItem("CART" , JSON.stringify(state))
            return [...state]
        }
        case types.CHANGE_CART:{
            var index = state.map(function(el) {
                return el.product.id;
              }).indexOf(action.cart.product.id);
            state[index].quantity = action.value
            localStorage.setItem("CART" , JSON.stringify(state))
            return [...state]
        }
        default :{
            for (let index = 0; index < state.length; index++) {
                state[index].quantity = parseInt(state[index].quantity)
            }
            return [...state]
        }
    }
}

export default myReducer