import React,{Component} from 'react';

class Header extends Component {
    constructor (props){
        super(props);
        this.state = {
            value : ""
        }
    }
    saveData = (event) =>{
        this.setState({
            value : event.target.value
        })
    }
    searchProduct = (event) =>{
        this.props.searchProduct(this.state.value)
    }
    render(){
        return (
            <nav className="navbar navbar-default navbar-fixed-top" role="navigation">
                <div className="navbar-header">
                    <button type="button" className="navbar-toggle" data-toggle="collapse" data-target=".navbar-ex1-collapse">
                        <span className="sr-only">Toggle navigation</span>
                        <span className="icon-bar"></span>
                        <span className="icon-bar"></span>
                        <span className="icon-bar"></span>
                    </button>
                    <a className="navbar-brand">Shopping cart</a>
                </div>
                <div className="collapse navbar-collapse navbar-ex1-collapse">
                    <ul className="nav navbar-nav">
                        <li className="active"><a>Home</a></li>
                    </ul>
                    <ul className="nav navbar-nav navbar-right">
                        <form className="navbar-form navbar-left" role="search">
                            <div className="form-group">
                                <input type="text" className="form-control" placeholder="Search"onChange = {this.saveData}/>
                            </div>
                            <button type="button" className="btn btn-default" onClick= {() => this.searchProduct()}>Search</button>
                        </form>
                        <li><a>login</a></li>
                        <li className="dropdown">
                            <a className="dropdown-toggle" data-toggle="dropdown">Connect with us<b className="caret"></b></a>
                            <ul className="dropdown-menu">
                                <li><a>Facebook</a></li>
                                <li><a>Instagram</a></li>
                                <li><a>Google+</a></li>
                                <li><a>Github</a></li>
                            </ul>
                        </li>
                    </ul>
                </div>
            </nav>
        )
    }
}

export default Header;

