import React,{Component} from 'react';
import {connect} from 'react-redux'
import Products from './../components/Products'
import Productitem from './../components/Productitem'
import PropTypes from 'prop-types';
import * as actions from './../actions/index'
import * as messages  from './../constants/Message'
import Header from './../components/Header'

class ProductsContainer extends Component {
    constructor (props) {
        super(props);
        this.state = {
            valueSearch : ""
        }
    }
    render(){
        return (
            <div>
            <Header searchProduct = {this.searchProduct}/>
                <hr></hr>
                <hr></hr>
            <Products>
                {this.showProductItem(this.props.products)}
            </Products>
            </div>
        )
    }
    showProductItem = (products) =>{
        var valueSearch =  this.state.valueSearch;
        products = products.filter(function(product){
            return product.name.toLowerCase().indexOf(valueSearch) !== -1
        })
        var elmProduct = <h1>Everything is sold out</h1>

        if (products.length > 0) {
            elmProduct =  products.map((product,index) =>{
                return <Productitem addToCart={this.addToCart} key = {index} infoProduct ={product}/>
            })   
        }
        return elmProduct
    }   
    searchProduct = (value) =>{
        this.setState({
            valueSearch : value
        })
    }   
    addToCart = (product) =>{
        this.props.addToCart(product)
        this.props.showMessage(messages.ADD_TO_CART_SUCCESS)
    }

}

ProductsContainer.propTypes = {
    products :PropTypes.arrayOf(
        PropTypes.shape({
            id : PropTypes.number.isRequired,
            name : PropTypes.string.isRequired,
            description  : PropTypes.string.isRequired,
            price : PropTypes.number.isRequired,
            inventory :PropTypes.number.isRequired,
            rating :PropTypes.number.isRequired,
            image : PropTypes.string.isRequired
        })
    ).isRequired,
    

}

const mapStateToProps = state =>{
    return {
        products : state.products
    }
}
const mapDispatchToProps = (dispatch, props) =>{
    return {
        addToCart : (product) =>{
            dispatch(actions.addToCart(product))
        },
        showMessage : (message) =>{
            dispatch(actions.showMessage(message))
        }
    }
}

export default connect(mapStateToProps,mapDispatchToProps)(ProductsContainer);
