import React, { Component } from 'react';

class Productitem extends Component {
    addToCart = () =>{
        this.props.addToCart(this.props.infoProduct)
    }
    render() {
        var {infoProduct} = this.props
        return (
            <div className="col-md-3 col-sm-6">
                <div className="product-grid2">
                    <div className="product-image2">
                        <a>
                            <img className="pic-1" src={infoProduct.image} />
                        </a>
                        <ul className="social">
                            <li><a data-tip={infoProduct.rating + " star"}><i className="fa fa-star"></i></a></li>
                        </ul>
                        <a className="add-to-cart" onClick= {this.addToCart}>Add to cart</a>
                    </div>
                    <div className="product-content">
                        <h3 className="title"><a>{infoProduct.name}</a></h3>
                        <h6 className= "description">availble {infoProduct.inventory}</h6>
                        <span className="price">{infoProduct.price} VND</span>
                    </div>
                </div>
            </div>
        )
    }
}

export default Productitem;
