import React, { Component } from 'react';

class Cartitem extends Component {
    changeCart = (event) =>{
        this.props.changeCart(this.props.infoCart ,event.target.value)
    }
    deleteItem = (cart) =>{
        this.props.deleteItem(cart)
    }
    render() {
        var {infoCart} = this.props

        return (
            <div className="row">
                <div className="col-xs-2"><img className="img-responsive" src={infoCart.product.image} />
                </div>
                <div className="col-xs-4">
                    <h4 className="product-name"><strong>{infoCart.product.name}</strong></h4><h4><small>{infoCart.product.description}</small></h4>
                </div>
                <div className="col-xs-6">
                    <div className="col-xs-6 text-right">
                        <h6><strong>{infoCart.product.price} VND<span className="text-muted"> x</span></strong></h6>
                    </div>
                    <div className="col-xs-4">
                        <input type="text" className="form-control input-sm" value = {infoCart.quantity} onChange = {this.changeCart}/>
                    </div>
                    <div className="col-xs-2">
                        <button type="button" className="btn btn-link btn-xs" onClick = {() => this.deleteItem(infoCart.product)}>
                            <span className="glyphicon glyphicon-trash"></span>
                        </button>
                    </div>
                </div>
            </div>
        )
    }
}

export default Cartitem;
