import React, { Component } from 'react';
import { connect } from 'react-redux'
import PropTypes from 'prop-types';
import Cart from './../components/Cart'
import Cartitem from './../components/Cartitem'
import Cartresult from './../components/Cartresult'
import Message from './../components/Message'
import * as actions from './../actions/index'
import * as messages from './../constants/Message'

class CartContainer extends Component {
    render() {
        var total = this.findTotal(this.props.cart)
        return (
            <Cart showCartResult = {this.showCartResult(total)} showMessage = {this.showMessage(this.props.message)}>
                {this.showCartItem(this.props.cart)} 
            </Cart>
        )
    }
    showCartItem = (cartItems) =>{
        var elmCart = messages.CART_EMPTY 
        if(cartItems.length > 0 ){
            elmCart = cartItems.map((cartItem, index) =>{
                return <div key = {index}><Cartitem deleteItem = {this.deleteItem} infoCart = {cartItem} changeCart = {this.changeCart}></Cartitem> <hr></hr></div> 
            })
        }
        return elmCart
    }
    findTotal = (cart) =>{
        var total = 0
        for (let index = 0; index < cart.length; index++) {
            total += (cart[index].product.price * cart[index].quantity)
        }
        return total
    } 
    showCartResult = (total) =>{
        return <Cartresult total = {total} ></Cartresult>
    }
    deleteItem = (cart) =>{
        this.props.deleteItem(cart)
        this.props.showMessage(messages.DELETE_ITEM_SUCCESS)
    }
    changeCart = (cart, value) =>{
        this.props.showMessage(messages.UPDATE_CART_SUCCESS)
        this.props.changeCart(cart, value)
    }
    showMessage = (message) =>{
        return <Message message = {message}></Message>
    }
    
}

CartContainer.propTypes = {
    cart: PropTypes.arrayOf(
        PropTypes.shape({
            product: PropTypes.shape({
                id: PropTypes.number.isRequired,
                name: PropTypes.string.isRequired,
                description: PropTypes.string.isRequired,
                price: PropTypes.number.isRequired,
                inventory: PropTypes.number.isRequired,
                rating: PropTypes.number.isRequired,
                image: PropTypes.string.isRequired
            }),
            quantity : PropTypes.number.isRequired
        })
    ).isRequired,

}

const mapStateToProps = state => {
    return {
        cart: state.cart,
        message : state.message
    }
}
const mapDispatchToProps = (dispatch, props) =>{
    return {
        deleteItem : (cart) =>{
            dispatch(actions.deleteItem(cart))
        },
        showMessage : (message) =>{
            dispatch(actions.showMessage(message))
        },
        changeCart : (cart, value) =>{
            dispatch(actions.changeCart(cart, value))
        }
    }
}

export default connect(mapStateToProps, mapDispatchToProps)(CartContainer);
